<template>
  <div>
    <h1>Kits</h1>
    <div>
      <filterItems
        :filtersOpt="[
          {
            type: 'Precio',
            name: 'Precio'
          }
        ]"
        :items.sync="items"
        :itemsToShow.sync="itemsToShow"
        action="/crear-kit"
      ></filterItems>

      <table-component
        lettersForId="KI"
        :itemsToShow="tableItems"
        @deleteItem="deleteKit"
        @editItem="editKit"
      >
        <template v-slot:productos="slotProps">
          <span
            v-if="slotProps.data.value.length < 3"
            v-html="slotProps.data.value.join('<br/>')"
          >
          </span>
          <div v-else>
            <span
              v-html="
                showComplete.find(
                  (el) => el.name === slotProps.data.item.nombre
                ).show_all
                  ? productsText
                      .find((el) => el.name === slotProps.data.item.nombre)
                      .p.map((el) => el.p)
                      .join('<br>')
                  : productsText.find(
                      (el) => el.name === slotProps.data.item.nombre
                    ).p.length
              "
            >
            </span>
            <br />
            <span
              @click="
                showComplete.find(
                  (el) => el.name === slotProps.data.item.nombre
                ).show_all = !showComplete.find(
                  (el) => el.name === slotProps.data.item.nombre
                ).show_all
              "
              class="text-info"
              style="cursor:pointer;"
            >
              Ver
              {{
                showComplete.find(
                  (el) => el.name === slotProps.data.item.nombre
                ).show_all
                  ? 'menos'
                  : 'ms'
              }}
            </span>
          </div>
        </template>
      </table-component>
    </div>
  </div>
</template>

<script>
  import { db } from '../../firebase/firebase.js';
  import { mapState } from 'vuex';
  import filterItems from '../../components/filterItems';
  import createId from '../../mixins/idCreator';
  import tableComponent from '../../components/table';
  export default {
    name: 'kits',
    mixins: [createId],
    components: {
      filterItems,
      tableComponent
    },
    data() {
      return {
        itemsToShow: [],
        showComplete: []
      };
    },
    created() {
      this.$store.dispatch('setKits');
    },
    asyncComputed: {
      async productsText() {
        let kits = this.kits;
        let res = {};
        if (kits.length > 0) {
          for (const iterator of kits) {
            let paragraph = [];
            let products = iterator.products;
            for (const key in products) {
              let el = products[key];
              let name = await db
                .ref(
                  `users/${this.$store.getters['user/userProfile'].uid}/products/${key}`
                )
                .once('value');
              if (!name.val()) continue;
              paragraph.push(
                `${name.val().name}: ${el.quantity.toLocaleString('es-AR')}`
              );
            }
            res[iterator.id] = paragraph;
            this.showComplete.push({
              name: iterator.name,
              show_all: false
            });
          }
          return res;
        }
      }
    },
    computed: {
      async costs() {
        let kitsPrices = {};
        if (!this.kits) return;
        for (const iterator of this.kits) {
          let costs = [];
          for (const key in iterator.products) {
            let product = iterator.products[key];
            let priceOfProduct =
              (await this.$store.dispatch('setProductPrice', key)) *
              product.quantity;
            if (priceOfProduct && !isNaN(priceOfProduct))
              costs.push(priceOfProduct);
            else {
              await db
                .ref(
                  `users/${this.$store.getters['user/userProfile'].uid}/kits/${iterator.id}/products/${key}`
                )
                .remove();
            }
          }
          if (costs.length > 0) kitsPrices[iterator.id] = costs;
          else
            await db
              .ref(
                `users/${this.$store.getters['user/userProfile'].uid}/kits/${iterator.id}`
              )
              .set(null);
        }
        return kitsPrices;
      },
      tableItems() {
        let items = this.itemsToShow;
        let res = items.map((_arrayElement) =>
          Object.assign({}, _arrayElement)
        );
        for (const item of res) {
          item.precio = `$${Number(
            item.precio.split('$')[1]
          ).toLocaleString()}`;
        }
        return res;
      },
      items() {
        let res = [];
        let kits = this.kits;
        kits.forEach(async (element) => {
          let price = (await this.costs)[element.id];
          if (!price) return;
          let products = this.productsText;
          let final_price = await this.getFinalPrice(element.id);
          res.push({
            ID: element.id,
            nombre: element.name,
            productos: products[element.id],
            precio: `$${price.reduce((a, b) => a + b, 0)}`,
            precio_final: `$${final_price.toLocaleString('es-AR')}`,
            ganancia: `$${(
              final_price - price.reduce((a, b) => a + b, 0)
            ).toLocaleString('es-AR')}`,
            opt: ''
          });
          this.itemsToShow = res;
        });
        return res;
      },

      ...mapState(['kits'])
    },
    methods: {
      async getFinalPrice(id) {
        let formula = (price, profit) =>
          ((1 + profit / 100) * price).toFixed(2);
        let prices = [];
        for (const key in this.$store.state.kits.find((el) => el.id === id)
          .products) {
          let el = this.$store.state.kits.find((el) => el.id === id).products[
            key
          ];
          let profit = await db
            .ref(
              `users/${this.$store.getters['user/userProfile'].uid}/products/${key}/profit`
            )
            .once('value');
          profit = profit.val();
          let price = await this.$store.dispatch('setProductPrice', key);
          prices.push(Number(formula(price * el.quantity, profit)));
        }
        return prices.reduce((a, b) => a + b, 0);
      },

      deleteKit({ item }) {
        db.ref(
          `users/${this.$store.getters['user/userProfile'].uid}/kits/${item.ID}`
        ).remove();
      },
      editKit({ item }) {
        this.$store.commit('changeName', item.ID);
        this.$router.push('ver-kit');
      }
    }
  };
</script>

<style scoped>
  h1 {
    text-align: center;
    padding-top: 20px;
  }
</style>
