<template>
  <div>
    <h1>Materias Primas</h1>
    <div>
      <filterItems
        :filtersOpt="[
          {
            type: 'Precio',
            name: 'Costo'
          },
          {
            name: 'Tipo',
            type: 'Tipo',
            propToCompare: 'tipo',
            types: [
              'Linn',
              'Tela Puntill',
              'Lienzo',
              'Cinta PP',
              'Cierre',
              'Etiquetas',
              'Bagn',
              'Cristal',
              'Deslizador',
              'Gabardina',
              'Mano de Obra'
            ]
          }
        ]"
        :items.sync="items"
        :itemsToShow.sync="itemsToShow"
        action="/crear-materia-prima"
      ></filterItems>
      <table-component
        lettersForId="MP"
        :itemsToShow="tableItems"
        @deleteItem="showAlertTrigger"
        @editItem="editSourceMaterial"
      >
      </table-component>
      <deleteConfirmation
        :sourceMaterialToEliminateId="sourceMaterialToEliminateId"
        :showAlert.sync="showAlert"
        v-if="showAlert"
        @deleteOperation="deleteSourceMaterial"
      />
    </div>
  </div>
</template>

<script>
  import { db } from '../../firebase/firebase.js';
  import { mapState } from 'vuex';
  import filterItems from '../../components/filterItems';
  import idCreator from '../../mixins/idCreator';
  import tableComponent from '../../components/table';
  import deleteConfirmation from '../../components/sourceMaterialsConfirmationPopUp';
  export default {
    name: 'source-materials',
    mixins: [idCreator],
    components: {
      filterItems,
      tableComponent,
      deleteConfirmation
    },
    data() {
      return {
        fields: [
          'ID',
          'nombre',
          'color',
          'calidad',
          'tipo',
          'proveedor',
          'precio',
          'opt'
        ],
        itemsToShow: [],
        sourceMaterialToEliminateId: undefined,
        showAlert: false
      };
    },
    created() {
      this.$store.dispatch('setSourceMaterials');
    },
    computed: {
      ...mapState(['sourceMaterials']),
      tableItems() {
        if (!this.itemsToShow) return [];
        let res = this.itemsToShow.map((_arrayElement) =>
          Object.assign({}, _arrayElement)
        );
        for (const item of res) {
          item.precio = `$${Number(
            item.precio.replace(/[^0-9&.]/g, '')
          ).toLocaleString('es-AR')}/${item.precio.split('/')[1]}`;
        }

        return res;
      },
      items() {
        let items = [];
        this.sourceMaterials.forEach((element) => {
          items.push({
            ID: element.id,
            nombre: element.name,
            color: element.characteristics.color,
            calidad: element.characteristics.quality,
            tipo: element.characteristics.type,
            proveedor: element.characteristics.supplier,
            precio: `$${element.price.amount}/${element.price.measurementUnit}`,
            opt: null
          });
        });
        this.itemsToShow = items;
        return items;
      }
    },
    methods: {
      async deleteSourceMaterial(id) {
        await db
          .ref(
            `users/${this.$store.getters['user/userProfile'].uid}/sourceMaterials/${id}`
          )
          .remove();
        this.showAlert = false;
      },
      showAlertTrigger({ item }) {
        this.sourceMaterialToEliminateId = item.ID;
        this.showAlert = true;
      },
      editSourceMaterial(sourceMaterial) {
        this.$store.commit('changeName', sourceMaterial.item.ID);
        this.$router.push('ver-materia-prima');
      }
    }
  };
</script>
