<template>
  <b-alert :show="showAlert">
    <div>
      <h4>Seguro que quers eliminarla?</h4>
      <h5 v-if="productsText">
        {{ productsText }}
      </h5>
    </div>
    <span>
      <b-button variant="primary" @click="removeItem()">Aceptar</b-button>
      <b-button variant="danger" @click="cancel()">Cancelar</b-button>
    </span>
  </b-alert>
</template>

<script>
  import { db } from '../firebase/firebase';
  export default {
    props: {
      sourceMaterialToEliminateId: {
        type: String
      },
      showAlert: {
        type: Boolean
      }
    },
    asyncComputed: {
      async productsText() {
        let res = [];
        let products_query = await db
          .ref(`users/${this.$store.getters['user/userProfile'].uid}/products`)
          .once('value');
        products_query = products_query.val();
        for (const key in products_query) {
          let el = products_query[key];
          let sourceMaterials = Object.keys(el.sourceMaterials);
          if (sourceMaterials.includes(this.sourceMaterialToEliminateId)) {
            res.push(el.name);
          }
        }
        let sourceMaterialName = this.$store.state.sourceMaterials.find(
          (el) => el.id === this.sourceMaterialToEliminateId
        ).name;
        return res.length !== 0
          ? `${res.join(', ')} lleva${
              res.length > 1 ? 'n' : ''
            } ${sourceMaterialName}`
          : '';
      }
    },
    methods: {
      removeItem() {
        this.$emit('deleteOperation', this.sourceMaterialToEliminateId);
      },
      cancel() {
        this.$emit('update:showAlert', false);
      }
    }
  };
</script>

<style scoped>
  .alert {
    position: absolute;
    left: 50%;
    top: 45%;
    text-align: center;
    transform: translate(-50%, -50%);
    display: flex;
    justify-content: space-evenly;
    flex-flow: column wrap;
    align-items: center;
    z-index: 9999;
    outline: 10000px solid rgba(0, 0, 0, 0.418);
    box-shadow: 0 0 0.4em #7e7e7e;
  }
  .alert > * {
    margin: 3vh 1vw;
    width: 100%;
  }
  .alert > span {
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
  }
  .alert > span > button {
    min-width: 100px;
    margin: 1vh 0;
  }
  @media screen and (max-width: 400px) {
    .alert {
      width: 100%;
    }
  }
</style>
