<template>
  <form @submit.prevent="sendData()" novalidate>
    <b-input-group prepend="Cliente" class="mb-2">
      <b-form-select
        :value="client"
        @input="updValue({ newValue: $event, prop: 'client' })"
      >
        <b-form-select-option
          :value="client.name"
          v-bind:key="client + ' ' + index"
          v-for="(client, index) in this.$store.state.clients"
        >
          {{ client.name }}
        </b-form-select-option>
      </b-form-select>
    </b-input-group>
    <b-input-group prepend="Importancia" class="mb-2">
      <b-form-select
        :options="importanceOpt"
        :value="importance"
        @input="updValue({ newValue: $event, prop: 'importance' })"
      >
      </b-form-select>
    </b-input-group>
    <b-input-group prepend="Fecha de Entrega" class="mb-2">
      <b-form-datepicker
        :value="deliveryTime"
        @input="updValue({ newValue: $event, prop: 'deliveryTime' })"
        :min="today"
        :date-format-options="{
          year: undefined,
          month: 'long',
          day: '2-digit',
          weekday: 'long'
        }"
        hide-header
        locale="es-AR"
        value-as-date
        placeholder=""
      ></b-form-datepicker>
    </b-input-group>

    <span id="sub-title">
      <h4 class="mb-1 mt-3">{{ subtitle }} que Lleva</h4>

      <toggle-button
        :value="true"
        :color="{
          checked: '#6c757d',
          unchecked: '#6c757d'
        }"
        :labels="{
          checked: 'Kits',
          unchecked: 'Productos'
        }"
        :width="120"
        :height="30"
        :font-size="15"
        v-model="seeKits"
        class="mb-3"
        @change="cleanInputs()"
      />
    </span>
    <addSubItemsTable
      :items="Object.values(itemsPlusTypeOfThem)"
      :fields="fields"
      :extraInfo="typeOfSelectedItem"
      :optionsForCreation="itemsOptions"
      :ids="ids"
      @getExtraInfo="setTypeOfSelectedItem"
      @addItem="pushItem"
      @updQuantity="updQuantity"
      @handleRemoval="deleteItem"
      ref="table"
    />
    <span>
      <b-button
        pill
        size="lg"
        variant="success"
        type="submit"
        :disabled="!isFormValid"
      >
        {{ this.id ? 'Guardar' : 'Agregar' }}
      </b-button>
      <cancelationButton redirectionForCancelation="/pedidos" />
    </span>
  </form>
</template>

<script>
  import { db } from '../../firebase/firebase.js';
  import cancelationButton from '../cancelationButton.vue';
  import addSubItemsTable from '../addSubItemsTable';
  export default {
    components: { cancelationButton, addSubItemsTable },
    name: 'order-layout',
    props: {
      client: {
        type: String
      },
      kit: {
        type: String
      },
      kits: {
        type: Object
      },
      products: {
        type: Object
      },
      quantity: {
        type: [String, Number]
      },
      importance: {
        type: String
      },
      deliveryTime: {
        type: [String, Date]
      },
      id: {
        type: String
      }
    },
    data() {
      return {
        fields: [
          {
            key: 'name',
            label: 'Nombre'
          },
          {
            key: 'quantity',
            label: 'Cantidad'
          },
          {
            key: 'productOrKit',
            label: 'Tipo'
          }
        ],
        importanceOpt: ['Alta', 'Media', 'Baja'],
        seeKits: true,
        typeOfSelectedItem: '',
        invalidProducts: [],
        invalidKits: [],
        prices: {}
      };
    },
    asyncComputed: {
      async price() {
        let productsPrices = [];
        let kitsPrices = [];

        let kits = this.kits;
        let products = this.products;

        for (const iterator in kits) {
          let kitQuantity = Number(this.kits[iterator].quantity);
          try {
            let costs = await this.getCostOfKit(iterator);
            let kitPrice = this.getFinalPriceOfKit(costs);
            let finalPrice = kitPrice * kitQuantity;
            kitsPrices.push(finalPrice);
          } catch (error) {
            this.invalidKits.push(iterator);
            this.prices[iterator] = kits[iterator].price;
          }
        }

        for (const key in products) {
          let costOfProduct = await this.$store.dispatch(
            'setProductPrice',
            key
          );
          if (!costOfProduct) {
            this.invalidProducts.push(key);
            this.prices[key] = products[key].price;
            continue;
          }
          let profit = await db
            .ref(
              `users/${this.$store.getters['user/userProfile'].uid}/products/${key}/profit/`
            )
            .once('value');
          profit = profit.val();
          let priceOfProduct = ((1 + profit / 100) * costOfProduct)
            .toFixed(2)
            .toLocaleString('es-AR');
          let quantity = this.products[key].quantity;
          productsPrices.push(priceOfProduct * quantity);
          this.prices[key] = priceOfProduct * quantity;
        }

        return (
          kitsPrices.reduce((a, b) => a + b, 0) +
          productsPrices.reduce((a, b) => a + b, 0) +
          this.invalidProducts
            .map((el) => this.prices[el])
            .reduce((a, b) => a + b, 0) +
          this.invalidKits
            .map((el) => this.prices[el])
            .reduce((a, b) => a + b, 0)
        );
      }
    },
    async created() {
      await this.$store.dispatch('setClients');
      await this.$store.dispatch('setKits');
      this.$store.dispatch('setProducts');
    },
    computed: {
      productsAndKitsToSave() {
        let products = this.products;
        let kits = this.kits;
        let res = { products: {}, kits: {} };
        for (const key in products) {
          console.log(this.prices);
          res.products[key] = {
            quantity: products[key].quantity,
            name: products[key].name,
            price: this.prices[key]
          };
        }
        for (const key in kits) {
          res.kits[key] = {
            quantity: kits[key].quantity,
            name: kits[key].name,
            price: this.prices[key]
          };
        }
        return res;
      },
      ids() {
        let result = [];
        let items = { ...this.products, ...this.kits };
        for (const key in items) {
          result.push({ name: items[key].name, id: key });
        }
        console.log(result);
        return result;
      },
      itemsOptions() {
        let kits = this.$store.state.kits.map((_arrayElement) =>
          Object.assign({}, _arrayElement)
        );
        let products = this.$store.state.products.map((_arrayElement) =>
          Object.assign({}, _arrayElement)
        );
        let res = this.seeKits
          ? [
              ...kits.map((el) => {
                return { value: { name: el.name, id: el.id }, text: el.name };
              })
            ]
          : [
              ...products.map((el) => {
                return { value: { name: el.name, id: el.id }, text: el.name };
              })
            ];
        return res;
      },
      isFormValid() {
        if (
          Object.values(this.kitsAndProducts).length > 0 &&
          this.deliveryTime &&
          this.importance &&
          this.client
        )
          return true;
        else return false;
      },
      today() {
        return new Date();
      },
      subtitle() {
        return this.seeKits ? 'Kits' : 'Productos';
      },
      kitsAndProducts() {
        return { ...this.kits, ...this.products };
      },
      itemsPlusTypeOfThem() {
        let result = Object.assign({}, this.kitsAndProducts);
        for (const key in result) {
          let isProduct = Object.values(this.$store.state.products)
            .map((el) => el.id)
            .includes(key);
          let isKit = Object.values(this.$store.state.kits)
            .map((el) => el.id)
            .includes(key);

          result[key].productOrKit = isProduct
            ? 'Producto'
            : isKit
            ? 'Kit'
            : '-';
          result[key].notChangeQuantity = !this.$store.state[
            isProduct ? 'products' : 'kits'
          ].some((el) => el.id === key);
        }
        return result;
      }
    },

    methods: {
      updValue({ newValue, prop }) {
        this.$emit(`update:${prop}`, newValue);
      },
      cleanInputs() {
        this.$refs.table.$data.quantity = '';
        this.$refs.table.$data.currentValueOfSelect = '';
        this.typeOfSelectedItem = '-';
      },
      async getCostOfKit(id) {
        console.log(id);
        let products = await db
          .ref(
            `users/${this.$store.getters['user/userProfile'].uid}/kits/${id}/products/`
          )
          .once('value');
        products = products.val();
        if (!products) throw new Error('Invalid Kit');
        let result = [];
        for (const key in products) {
          let product = products[key];
          let priceOfProduct =
            (await this.$store.dispatch('setProductPrice', key)) *
            product.quantity;
          result.push({
            id: key,
            price: priceOfProduct
          });
        }
        return result;
      },
      getFinalPriceOfKit(productsWithCosts) {
        let kits_prices = [];
        let formula = (price, profit) =>
          ((1 + profit / 100) * price).toFixed(2).toLocaleString('es-AR');
        let prices = productsWithCosts.map((el) => {
          let profit = this.$store.state.products.find((i) => (i.id = el.id))
            .profit;
          return Number(formula(el.price, profit));
        });
        kits_prices.push(Number(prices.reduce((a, b) => a + b, 0)));
        return kits_prices;
      },

      updQuantity({ newQuantity, id }) {
        let prop = this.kits[id] ? this.kits : this.products;
        let copy = Object.assign({}, prop);
        if (!newQuantity || newQuantity === 0) {
          console.log('here');
          delete copy[id];
          this.$emit(`update:${prop}`, copy);
        } else {
          copy[id].quantity = newQuantity;
          this.$emit(`update:${prop}`, copy);
        }
      },
      sendData() {
        let newKey = db
          .ref()
          .child('orders')
          .push().key;
        console.log(this.id);
        let now = new Date();
        let orderData = {
          deliveryTime: this.deliveryTime.toString(),
          client: this.client,
          kits: this.productsAndKitsToSave.kits,
          products: this.productsAndKitsToSave.products,
          createdAt: now.toString(),
          importance: this.importance,
          status: 'Pendiente',
          price: this.price
        };
        let updates = {};
        updates[
          `users/${this.$store.getters['user/userProfile'].uid}/orders/${this
            .id || newKey}`
        ] = orderData;
        db.ref().update(updates);
        this.$router.push('pedidos');
      },
      pushItem({ quantity, element }) {
        console.log(quantity);
        if (this.seeKits) {
          this.$set(this.kits, element.id, {
            name: element.name,
            quantity: quantity
          });
        } else {
          this.$set(this.products, element.id, {
            name: element.name,
            quantity: quantity
          });
        }
      },
      setTypeOfSelectedItem(name) {
        if (!name) return '';
        this.typeOfSelectedItem = this.$store.state.products.some(
          (el) => el.name === name
        )
          ? 'Producto'
          : 'Kit';
      },
      deleteItem({ item }) {
        let id = this.ids.find((el) => (el.name = item.name)).id;
        let object = this.kits[id] ? this.kits : this.products;
        this.$delete(object, id);
      }
    }
  };
</script>

<style scoped>
  form {
    display: flex;
    flex-direction: column;
    padding-top: 20px;
    justify-content: space-around;
    align-items: center;
    min-height: 90vh;
  }
  .input-group {
    width: 80vw;
  }
  #sub-title {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  form > span {
    display: flex;
    justify-content: space-around;
    width: 100%;
  }
</style>
