<template>
  <products-layout
    :name.sync="name"
    :color.sync="color"
    :quality.sync="quality"
    :type.sync="type"
    :profit.sync="profit"
    :sourceMaterial.sync="sourceMaterial"
    :sourceMaterials.sync="sourceMaterials"
    :id.sync="id"
    :quantity.sync="quantity"
    :size.sync="size"
  ></products-layout>
</template>

<script>
  import productsLayout from '../../components/layouts/productsLayout.vue';
  export default {
    components: { productsLayout },
    created() {
      let product_query = this.$store.state.products.find(
        (el) => el.id === this.$store.state.nameOfActualItem
      );
      ({
        name: this.name,
        profit: this.profit,
        type: this.type
      } = product_query);
      this.sourceMaterials = this.formatSourceMaterials(
        product_query.sourceMaterials
      );
      this.size = product_query.characteristics.size;
      this.color = product_query.characteristics.color;
      this.quality = product_query.characteristics.quality;
      this.id = this.$store.state.nameOfActualItem;
    },
    methods: {
      formatSourceMaterials(sourceMaterials) {
        let res = {};
        for (const key in sourceMaterials) {
          let el = this.$store.state.sourceMaterials.find(
            (el) => el.id === key
          );
          let { name } = el;
          console.log(el);
          res[key] = {
            name: name,
            measurementUnit: el.price.measurementUnit,
            quantity: sourceMaterials[key].quantity
          };
        }
        return res;
      }
    },
    data() {
      return {
        name: '',
        sourceMaterials: {},
        sourceMaterial: '',
        quantity: '',
        size: '',
        color: '',
        quality: '',
        profit: '',
        id: '',
        type: ''
      };
    }
  };
</script>
