<template>
  <form @submit.prevent="sendData()" novalidate>
    <div id="container" v-if="this.$store.state.sourceMaterials !== undefined">
      <b-input-group prepend="Nombre" class="mb-2">
        <b-input
          :value="name"
          @input="updValue({ newValue: $event, prop: 'name' })"
          :style="[
            $v.name.$error ? { border: '2px solid rgb(255, 36, 36)' } : null
          ]"
          type="text"
        ></b-input
      ></b-input-group>

      <b-input-group prepend="Tipo de Producto">
        <b-form-select
          :value="type"
          @input="updValue({ newValue: $event, prop: 'type' })"
          :options="types"
        >
        </b-form-select
      ></b-input-group>
      <b-input-group prepend="Tamao" class="mb-2 mt-3">
        <b-form-select
          :value="size"
          @input="updValue({ newValue: $event, prop: 'size' })"
          :options="sizes"
        >
        </b-form-select>
      </b-input-group>
      <b-input-group prepend="Color" class="mb-2">
        <b-form-select
          :value="color"
          @input="updValue({ newValue: $event, prop: 'color' })"
          :options="colors"
        >
        </b-form-select>
      </b-input-group>
      <b-input-group class="mb-2" prepend="Calidad">
        <b-form-select
          :value="quality"
          @input="updValue({ newValue: $event, prop: 'quality' })"
          :options="qualities"
        >
        </b-form-select>
      </b-input-group>
      <b-input-group
        prepend="Margen de Ganancia"
        append="
        %
        "
      >
        <b-input
          :value="profit"
          @input="
            updValue({
              newValue: Number($event.replace(/,/g, '.')),
              prop: 'profit'
            })
          "
          type="number"
        >
        </b-input>
      </b-input-group>
      <h4 class="mt-4">
        Materias Primas que Lleva
      </h4>
      <addSubItemsTable
        :extraInfo="measurementUnitOfSelectedSourceMaterial"
        :fields="fields"
        :optionsForCreation="sourceMaterialsOpts"
        :items="Object.values(sourceMaterials)"
        :ids="ids"
        @updQuantity="updQuantity"
        @addItem="pushSourceMaterial"
        @handleRemoval="deleteSourceMaterial"
      />
      <span>
        <b-button
          :disabled="
            $v.$invalid === true ||
              !isNameAvailable({
                val: this.name,
                prop: 'products',
                idOfItemThatIsBeingEdit: this.id
              })
          "
          variant="success"
          size="lg"
          pill
          class="mb-2"
          type="submit"
        >
          {{ this.id ? 'Guardar' : 'Agregar' }}
        </b-button>
        <cancelationButton redirectionForCancelation="/productos" />
      </span>
    </div>
    <span v-else>
      <h2 style="text-align: center;">
        Todava no tens Materias Primas Creadas
        <router-link
          style="color: rgb(10, 92, 173); text-align:center; margin-top: 10px;"
          to="/crear-materia-prima"
        >
          <h2 style="font-size: 4rem;">
            Crealas!
          </h2>
        </router-link>
      </h2>
    </span>
  </form>
</template>

<script>
  import { db } from '../../firebase/firebase.js';
  import { required } from 'vuelidate/lib/validators';
  import cancelationButton from '../../components/cancelationButton.vue';
  import addSubItemsTable from '../../components/addSubItemsTable';
  import validateNameFunction from '../../mixins/validateName';
  export default {
    mixins: [validateNameFunction],
    components: { cancelationButton, addSubItemsTable },
    name: 'products-layout',
    props: {
      name: {
        type: String
      },
      sourceMaterials: {
        type: Object
      },
      sourceMaterial: {
        type: [String, Object]
      },
      quantity: {
        type: [String, Number]
      },
      size: {
        type: String
      },
      color: {
        type: String
      },
      quality: {
        type: String
      },
      profit: {
        type: [String, Number]
      },
      type: {
        type: String
      },
      id: {
        type: String
      }
    },
    data() {
      return {
        fields: [
          {
            key: 'name',
            label: 'Nombre'
          },
          {
            key: 'quantity',
            label: 'Cantidad'
          },
          {
            key: 'measurementUnit',
            label: 'Unidad de Medida'
          }
        ],
        submitStatus: null,
        sizes: ['Grande', 'Mediano', 'Pequeo'],
        colors: ['Blanco', 'Azul', 'Beige'],
        qualities: ['Alta', 'Media', 'Baja'],
        measurementUnitOfSelectedSourceMaterial: undefined,
        types: ['Sss', 'Aaaa']
      };
    },
    created() {
      this.$store.dispatch('setSourceMaterials');
    },
    validations: {
      name: {
        required
      },

      profit: {
        required,
        validateNumber: (value) => value > 0
      },
      size: {
        required
      },
      color: {
        required
      },
      quality: {
        required
      },
      type: {
        required
      },
      sourceMaterials: {
        validateObject: (value) => Object.values(value).length > 0
      }
    },

    computed: {
      sourceMaterialsToSave() {
        let res = {};
        let sourceMaterials = this.sourceMaterials;
        for (const key in sourceMaterials) {
          res[key] = {
            quantity: sourceMaterials[key].quantity
          };
        }
        return res;
      },
      ids() {
        let result = [];
        let sourceMaterials = this.sourceMaterials;
        for (const key in sourceMaterials) {
          const element = Object.assign({}, sourceMaterials)[key];
          result.push({ name: element.name, id: key });
        }
        return result;
      },
      sourceMaterialsOpts() {
        return this.$store.state.sourceMaterials.map((el) => {
          return { value: { name: el.name, id: el.id }, text: el.name };
        });
      }
    },
    methods: {
      getExtraInfo(data) {
        this.measurementUnitOfSelectedSourceMaterial = this.$store.state.sourceMaterials.find(
          (el) => el.name === data.name
        ).price.measurementUnit;
      },
      updValue({ newValue, prop, notEvaluate }) {
        if (!notEvaluate) this.$v[prop].$touch;

        this.$emit(`update:${prop}`, newValue);
      },
      async updQuantity({ newQuantity, id }) {
        let copy = Object.assign({}, this.sourceMaterials);
        if (!newQuantity || newQuantity === 0) {
          delete copy[id];
          this.$emit('update:sourceMaterials', copy);
        } else {
          copy[id].quantity = newQuantity;
          this.$emit('update:sourceMaterials', copy);
        }
      },
      async sendData() {
        this.$v.$touch();
        if (this.$v.$invalid) {
          this.submitStatus = 'ERROR';
        } else {
          this.submitStatus = 'OK';
          var newKey = db
            .ref()
            .child('products')
            .push().key;
          let productData = {
            name: this.name,
            type: this.type,
            sourceMaterials: this.sourceMaterialsToSave,
            characteristics: {
              color: this.color,
              size: this.size,
              quality: this.quality
            },
            profit: this.profit
          };
          let updates = {};
          updates[
            `users/${
              this.$store.getters['user/userProfile'].uid
            }/products/${this.id || newKey}`
          ] = productData;
          await db.ref().update(updates);
          this.$router.push('productos');
        }
      },
      pushSourceMaterial({ quantity, element }) {
        this.$set(this.sourceMaterials, element.id, {
          name: element.name,
          quantity: quantity,
          measurementUnit: this.$store.state.sourceMaterials.find(
            (el) => el.id === element.id
          ).price.measurementUnit
        });
      },
      deleteSourceMaterial({ item }) {
        this.$delete(
          this.sourceMaterials,
          this.ids.find((el) => (el.name = item.name)).id
        );
      }
    }
  };
</script>

<style scoped>
  #container {
    display: flex;
    flex-direction: column;
    padding-top: 20px;
    min-height: 92.3vh;
    justify-content: space-around;
    align-items: center;
  }

  form > span {
    display: flex;
    align-items: center;
    height: 85vh;
    justify-content: center;
  }

  .input-group {
    width: 80vw;
  }
  form > div > span {
    display: flex;
    justify-content: space-around;
    width: 100%;
  }
</style>
