<template>
  <div>
    <h1>Productos</h1>
    <div>
      <filterItems
        :filtersOpt="[
          {
            type: 'Precio',
            name: 'Costo'
          },
          {
            name: 'Tipo',
            type: 'Tipo',
            propToCompare: 'tipo',
            types: ['Sss', 'Aaaa']
          }
        ]"
        :items.sync="items"
        :itemsToShow.sync="itemsToShow"
        action="/crear-producto"
      ></filterItems>

      <table-component
        lettersForId="PR"
        :itemsToShow="tableItems"
        @deleteItem="deleteProduct"
        @editItem="editProduct"
      >
        <template #materias_primas="slotProps">
          <template v-if="slotProps.data.value.length < 4">
            <span> {{ Array.from(slotProps.data.value).join('\n') }}</span>
          </template>
          <div v-else>
            <span>
              {{
                !showComplete.find((el) => el.id === slotProps.data.item.ID)
                  .show_all
                  ? sourceMaterialsText[slotProps.data.item.ID].length
                  : sourceMaterialsText[slotProps.data.item.ID].join('\n')
              }}
            </span>
            <br
              v-if="
                showComplete.find((el) => el.id === slotProps.data.item.ID)
                  .show_all
              "
            />
            <span
              @click="
                showComplete.find(
                  (el) => el.id === slotProps.data.item.ID
                ).show_all = !showComplete.find(
                  (el) => el.id === slotProps.data.item.ID
                ).show_all
              "
              class="text-info mt-5"
              style="cursor:pointer;"
            >
              Ver
              {{
                !showComplete.find((el) => el.id === slotProps.data.item.ID)
                  .show_all
                  ? 'ms'
                  : 'menos'
              }}
            </span>
          </div>
        </template>
      </table-component>
    </div>
  </div>
</template>

<script>
  import { db } from '../../firebase/firebase.js';
  import { mapState } from 'vuex';
  import filterItems from '../../components/filterItems';
  import idCreator from '../../mixins/idCreator';
  import tableComponent from '../../components/table';
  export default {
    name: 'products',
    mixins: [idCreator],
    created() {
      this.$store.dispatch('setProducts');
    },
    components: {
      filterItems,
      tableComponent
    },
    data() {
      return {
        fields: [
          'ID',
          'nombre',
          'tipo',
          'color',
          'calidad',
          'tamao',
          'materias_primas',
          'precio',
          'precio_final',
          'margen_de_ganancia',
          'ganancia',
          'opt'
        ],
        itemsToShow: [],
        showComplete: []
      };
    },
    asyncComputed: {
      async sourceMaterialsText() {
        let products = this.products;
        let sourceMaterials = {};
        for (const nameOfActualItem of products) {
          let paragraphs = [];
          let sourceMaterialsOfProduct = nameOfActualItem.sourceMaterials;

          for (const key in sourceMaterialsOfProduct) {
            let el = sourceMaterialsOfProduct[key];
            let name = await db
              .ref(
                `users/${this.$store.getters['user/userProfile'].uid}/sourceMaterials/${key}`
              )
              .once('value');
            if (!name.val()) continue;
            paragraphs.push(
              `${name.val().name}: ${Number(el.quantity).toLocaleString(
                'es-AR'
              )}`
            );
          }

          sourceMaterials[nameOfActualItem.id] = paragraphs;
          this.showComplete.push({
            id: nameOfActualItem.id,
            show_all: false
          });
        }
        return sourceMaterials;
      }
    },
    computed: {
      tableItems() {
        if (!this.itemsToShow) return [];
        let res = this.itemsToShow.map((_arrayElement) =>
          Object.assign({}, _arrayElement)
        );
        for (const item of res) {
          item.precio = `$${Number(
            item.precio.split('$')[1]
          ).toLocaleString()}`;
          item.opt = '';
        }
        return res;
      },

      items() {
        let texts = this.sourceMaterialsText;
        if (!texts) return;
        if (this.products.length > 0) {
          let items = [];
          this.products.forEach(async (element) => {
            try {
              let price = Number(
                (await this.prices())[element.id].reduce((a, b) => a + b, 0)
              );
              const priceWithProft = Number(
                await this.getFinalPrice(price, element.profit)
              );
              if (!element) return false;
              items.push({
                ID: element.id,
                nombre: element.name,
                tipo: element.type,
                color: element.characteristics.color,
                calidad: element.characteristics.quality,
                tamao: element.characteristics.size,
                materias_primas: texts[element.id],
                precio: `$${price}`,
                precio_final: `$${priceWithProft.toLocaleString('es-AR')}`,
                margen_de_ganancia: `${element.profit.toLocaleString(
                  'es-AR'
                )}%`,
                ganancia: `$${Number(
                  (priceWithProft - price).toFixed(2)
                ).toLocaleString('es-AR')}`
              });
            } catch (error) {
              return;
            }
          });
          this.itemsToShow = items;
          return items;
        }
      },
      ...mapState(['products'])
    },
    methods: {
      async prices() {
        let result = {};
        for (const key in this.products) {
          const element = this.products[key];
          try {
            let price = await this.$store.dispatch(
              'setProductPrice',
              element.id
            );
            console.log(price);
            result[element.id] = price;
          } catch (error) {
            throw new Error('Invalid Item');
          }
        }

        return result;
      },
      async getFinalPrice(price, profit) {
        return ((1 + profit / 100) * price).toFixed(2).toLocaleString('es-AR');
      },
      deleteProduct(product) {
        db.ref(
          `users/${this.$store.getters['user/userProfile'].uid}/products/${product.item.ID}`
        ).remove();
      },
      editProduct(productName) {
        this.$store.commit('changeName', productName.item.ID);
        this.$router.push('ver-producto');
      }
    }
  };
</script>

<style scoped>
  h1 {
    text-align: center;
    padding-top: 20px;
  }
</style>
