<template>
  <b-table responsive :fields="fieldsWithTrashButton" :items="itemsToShow">
    <template #top-row>
      <td class="w-25">
        <b-form-select :options="validNameOpt" v-model="currentValueOfSelect">
        </b-form-select>
      </td>

      <td class="w-50">
        <b-input v-model="quantity" type="number"></b-input>
      </td>
      <td>{{ extraInfo || '-' }}</td>

      <td>
        <b-button
          :disabled="!quantity || !currentValueOfSelect"
          variant="outline-info"
          @click="addItem()"
          ><b-icon scale="1.3" icon="plus">{{
            !quantity || currentValueOfSelect
          }}</b-icon></b-button
        >
      </td>
    </template>
    <template #head(trashButtonColumn)> {{ `` }}</template>

    <template #cell(quantity)="data">
      <b-input
        v-if="!data.item.isForShowingTheTable && !data.item.notChangeQuantity"
        @input="updQuantity($event, data)"
        :value="data.value.toString().replaceAll('.', ',')"
      ></b-input>
      <span v-else> {{ data.value }}</span>
    </template>
    <template #cell(trashButtonColumn)="data">
      <b-icon
        style="cursor:pointer;"
        class="ml-3"
        icon="trash"
        scale="1.8"
        v-if="!data.item.isForShowingTheTable"
        @click.stop="handleRemoval(data)"
      >
      </b-icon
    ></template>
  </b-table>
</template>

<script>
  export default {
    props: {
      items: Array,
      fields: Array,
      optionsForCreation: Array,
      extraInfo: String,
      ids: Array
    },
    data() {
      return {
        currentValueOfSelect: undefined,
        quantity: undefined
      };
    },
    watch: {
      currentValueOfSelect(newValue) {
        this.$emit('getExtraInfo', newValue);
      }
    },
    computed: {
      validNameOpt() {
        let opts = this.optionsForCreation;
        let items = this.items;
        let merge = [
          opts.map((el) => el.value.name),
          items.map((el) => el.name)
        ].flat();
        let duplicates = merge.filter(
          (item, index) => merge.indexOf(item) != index
        );
        let result = merge.filter(
          (item) => !duplicates.some((el) => el === item)
        );
        result = opts.filter((el) => result.includes(el.value.name));
        return result;
      },
      itemsToShow() {
        //Add item to Items so the table will show despite having any items
        let items = this.items.map((_arrayElement) => {
          let res = Object.assign({}, _arrayElement);
          res.trashButtonColumn = ' ';
          return res;
        });
        return [...items, { isForShowingTheTable: true }];
      },
      fieldsWithTrashButton() {
        let fields = this.fields.map((_arrayElement) =>
          Object.assign({}, _arrayElement)
        );
        fields.push('trashButtonColumn');

        return fields;
      }
    },
    methods: {
      addItem() {
        this.$emit('addItem', {
          quantity: Number(this.quantity.replace(/,/g, '.')),
          element: this.currentValueOfSelect
        });
        this.quantity = '';
        this.currentValueOfSelect = '';
      },
      updQuantity(newQuantity, data) {
        this.$emit('updQuantity', {
          newQuantity: Number(newQuantity.replace(/,/g, '.')),
          id: this.ids?.find((el) => el.name === data.item.name).id,
          name: data.item.name
        });
      },
      handleRemoval(info) {
        this.$emit('handleRemoval', info);
      }
    }
  };
</script>

<style></style>
