import Vue from 'vue';
import Vuex from 'vuex';
import { db } from '../firebase/firebase.js';
import user from './user/index.js';

Vue.use(Vuex);

export default new Vuex.Store({
  modules: {
    user
  },
  state: {
    products: [],
    sourceMaterials: [],
    clients: [],
    kits: [],
    suppliers: [],
    nameOfActualItem: undefined,
    orders: undefined
  },
  mutations: {
    changeName(state, newName) {
      state.nameOfActualItem = newName;
    }
  },
  actions: {
    setProductPrice: async (context, name) => {
      let sourceMaterialsQuery = await db
        .ref(
          `users/${context.state.user.userProfile.uid}/products/${name}/sourceMaterials`
        )
        .once('value');
      sourceMaterialsQuery = sourceMaterialsQuery.val();
      if (!sourceMaterialsQuery) return;
      let prices = [];

      for (const key in sourceMaterialsQuery) {
        let price = await db
          .ref(
            `users/${context.state.user.userProfile.uid}/sourceMaterials/${key}/price/amount`
          )
          .once('value');
        if (price.val()) {
          let el = sourceMaterialsQuery[key];
          let quantity = el.quantity;

          price = price.val();
          prices.push(quantity * price);
        } else {
          await db
            .ref(
              `users/${context.state.user.userProfile.uid}/products/${name}/sourceMaterials/${key}`
            )
            .set(null);
          continue;
        }
      }
      if (prices.length === 0) {
        await db
          .ref(`users/${context.state.user.userProfile.uid}/products/${name}`)
          .set(null);
      } else {
        return prices;
      }
    },
    setProducts: ({ state }) => {
      let products = [];

      db.ref(`users/${state.user.userProfile.uid}/products`).on(
        'value',
        (snapshot) => {
          products = [];
          if (snapshot.val()) {
            for (const key in snapshot.val()) {
              const product = snapshot.val()[key];
              products.push({
                name: product.name,
                type: product.type,
                characteristics: product.characteristics,
                id: key,
                sourceMaterials: product.sourceMaterials,
                profit: product.profit
              });
              state.products = products;
            }
          } else {
            state.products = [];
          }
        }
      );
    },
    setSourceMaterials: ({ state }) => {
      let sourceMaterials = [];

      db.ref(`users/${state.user.userProfile.uid}/sourceMaterials`).on(
        'value',
        (snapshot) => {
          sourceMaterials = [];
          if (snapshot.val()) {
            for (const key in snapshot.val()) {
              const sourceMaterial = snapshot.val()[key];
              sourceMaterials.push({
                name: sourceMaterial.name,
                characteristics: sourceMaterial.characteristics,
                price: sourceMaterial.price,
                id: key
              });
              state.sourceMaterials = sourceMaterials;
            }
          } else {
            state.sourceMaterials = [];
          }
        }
      );
    },
    setClients: ({ state }) => {
      let clients = [];

      db.ref(`users/${state.user.userProfile.uid}/clients`).on(
        'value',
        (snapshot) => {
          clients = [];
          if (snapshot.val()) {
            for (const key in snapshot.val()) {
              const client = snapshot.val()[key];
              clients.push({
                name: client.name,
                notes: client.notes,
                loc: client.loc,
                address: client.address,
                phoneNumber: client.phoneNumber,
                id: key,
                company: client.company
              });
              state.clients = clients;
            }
          } else {
            state.clients = [];
          }
        }
      );
    },
    setKits: ({ state }) => {
      let kits = [];

      db.ref(`users/${state.user.userProfile.uid}/kits`).on(
        'value',
        (snapshot) => {
          kits = [];
          if (snapshot.val()) {
            for (const key in snapshot.val()) {
              const kit = snapshot.val()[key];
              kits.push({
                name: kit.name,
                products: kit.products,
                id: key
              });
              state.kits = kits;
            }
          } else {
            state.kits = [];
          }
        }
      );
    },
    setOrders: ({ state }) => {
      let orders = [];

      db.ref(`users/${state.user.userProfile.uid}/orders`).on(
        'value',
        (snapshot) => {
          orders = [];
          if (snapshot.val()) {
            for (const key in snapshot.val()) {
              const order = snapshot.val()[key];
              orders.push({
                deliveryTime: order.deliveryTime,
                client: order.client,
                createdAt: order.createdAt,
                id: key,
                kits: order.kits,
                products: order.products,
                importance: order.importance,
                price: order.price,
                status: order.status
              });
              state.orders = orders;
            }
          } else {
            state.orders = [];
          }
        }
      );
    },
    setSuppliers: ({ state }) => {
      let suppliers = [];
      db.ref(`users/${state.user.userProfile.uid}/suppliers`).on(
        'value',
        (snapshot) => {
          suppliers = [];
          if (snapshot.val()) {
            for (const key in snapshot.val()) {
              const supplier = snapshot.val()[key];
              suppliers.push({
                name: supplier.name,
                notes: supplier.notes,
                loc: supplier.loc,
                address: supplier.address,
                phoneNumber: supplier.phoneNumber,
                id: key,
                company: supplier.company
              });
              state.suppliers = suppliers;
            }
          } else {
            state.suppliers = [];
          }
        }
      );
    }
  }
});
