<template>
  <kits-layout
    :nameKit.sync="nameKit"
    :product.sync="product"
    :products.sync="products"
    :quantity.sync="quantity"
    :id.sync="id"
  ></kits-layout>
</template>

<script>
  import kitsLayout from '../../components/layouts/kitsLayout.vue';
  export default {
    components: { kitsLayout },
    data() {
      return {
        nameKit: '',
        product: '',
        products: {},
        quantity: '',
        id: ''
      };
    },
    methods: {
      formatProducts(products) {
        console.log(2);
        let res = {};
        for (const key in products) {
          let el = this.$store.state.products.find((el) => el.id === key);
          let { name } = el;
          res[key] = {
            name: name,
            quantity: products[key].quantity,
            id: key
          };
          console.log(key);
        }
        return res;
      }
    },
    async created() {
      let kit_info = this.$store.state.kits.find(
        (el) => el.id === this.$store.state.nameOfActualItem
      );
      this.nameKit = kit_info.name;
      this.id = kit_info.id;
      this.products = this.formatProducts(kit_info.products);
    }
  };
</script>
