import store from '../store/index';

export default {
  methods: {
    isNameAvailable(info) {
      const { val, prop } = info;
      if (!info.idOfItemThatIsBeingEdit)
        return !store.state[prop].some((el) => el.name === val);
      else {
        let el = store.state[prop].find((el) => el.name === val);
        if (!el) return true;
        else {
          console.log(el.id, info.idOfItemThatIsBeingEdit);
          if (el.id === info.idOfItemThatIsBeingEdit) {
            return true;
          } else {
            return false;
          }
        }
      }
    }
  }
};
