<template>
  <form @submit.prevent="sendData()" novalidate>
    <b-input-group class="mb-3" prepend="Nombre del Kit">
      <b-input
        @input="updValue({ newValue: $event, prop: 'nameKit' })"
        type="text"
        :value="nameKit"
      ></b-input
    ></b-input-group>
    <div class="products-form">
      <h3 class="mb-4 mt-3">Productos</h3>
      <productsTable
        @handleRemoval="deleteProduct"
        @updQuantity="updQuantity"
        @addItem="pushProduct"
        :ids="ids"
        :optionsForCreation="productsOpt"
        :items="Object.values(products)"
        :fields="fields"
      />
    </div>
    <span>
      <b-button
        pill
        size="lg"
        class="mb-2"
        variant="success"
        type="submit"
        :disabled="
          !nameKit ||
            Object.keys(products).length === 0 ||
            !isNameAvailable({
              val: this.nameKit,
              prop: 'kits',
              idOfItemThatIsBeingEdit: this.id
            })
        "
      >
        {{ this.id ? 'Guardar' : 'Agregar' }}</b-button
      >
      <cancelationButton redirectionForCancelation="/kits" />
    </span>
  </form>
</template>

<script>
  import { db } from '../../firebase/firebase.js';
  import cancelationButton from '../../components/cancelationButton.vue';
  import productsTable from '../addSubItemsTable';
  import validateNameFunction from '../../mixins/validateName';
  export default {
    mixins: [validateNameFunction],

    components: { cancelationButton, productsTable },
    name: 'kits-layout',
    props: {
      nameKit: {
        type: String
      },
      products: {
        type: Object
      },
      product: {
        type: String
      },
      quantity: {
        type: [String, Number]
      },
      id: {
        type: String
      }
    },
    created() {
      this.$store.dispatch('setProducts');
    },
    data() {
      return {
        kitInfo: undefined,
        fields: [
          {
            key: 'name',
            label: 'Nombre'
          },
          {
            key: 'quantity',
            label: 'Cantidad'
          },
          {
            key: 'extra_info',
            label: ''
          }
        ]
      };
    },
    computed: {
      productsToSave() {
        let res = {};
        let products = this.products;
        for (const key in products) {
          let el = products[key];
          res[el.id] = {
            quantity: el.quantity
          };
        }
        return res;
      },
      productsOpt() {
        return this.$store.state.products.map((el) => {
          return { value: { name: el.name, id: el.id }, text: el.name };
        });
      },
      ids() {
        let result = [];
        let sourceMaterials = this.products;
        for (const key in sourceMaterials) {
          const element = Object.assign({}, sourceMaterials)[key];
          result.push({ name: element.name, id: key });
        }
        return result;
      }
    },
    methods: {
      updValue({ newValue, prop }) {
        this.$emit(`update:${prop}`, newValue);
      },
      updQuantity({ newQuantity, id }) {
        let copy = Object.assign({}, this.products);
        if (!newQuantity || newQuantity === 0) {
          delete copy[id];
          this.$emit('update:products', copy);
        } else {
          copy[id].quantity = newQuantity;
          this.$emit('update:products', copy);
        }
      },
      async sendData() {
        this.submitStatus = 'OK';
        let newKey = db
          .ref()
          .child('products')
          .push().key;
        let kitData = {
          name: this.nameKit,
          products: this.productsToSave
        };
        console.dir(kitData);
        let updates = {};
        updates[
          `users/${this.$store.getters['user/userProfile'].uid}/kits/${this
            .id || newKey}`
        ] = kitData;
        await db.ref().update(updates);
        this.$router.push('kits');
      },
      pushProduct({ quantity, element }) {
        console.log(element);
        this.$set(this.products, element.name, {
          quantity: quantity,
          id: element.id,
          name: element.name
        });
      },
      deleteProduct({ item }) {
        this.$delete(this.products, item.id);
      }
    }
  };
</script>

<style scoped>
  form > span {
    display: flex;
    justify-content: space-around;
    width: 100%;
  }
  .card {
    min-width: 50vw;
    text-align: center;
  }
  form {
    display: flex;
    flex-direction: column;
    padding-top: 20px;
    justify-content: space-around;
    align-items: center;
    min-height: 90vh;
  }
  .input-group {
    width: 80vw;
  }
  h3 {
    font-weight: 500;
    font-size: 2em;
    text-align: center;
  }
  .products-form {
    width: 100%;
  }
</style>
